	module.exports = {"data":{"site":{"siteMetadata":{"title":"Solar Sailer","author":"Matthieu Oger"}},"markdownRemark":{"html":"<p>The <a href=\"http://www.upup.fm/\">Up Up Down Down</a> podcast has a good episode with Asher Vollmer.</p>\n<p><a href=\"http://www.upup.fm/show/a-tiny-game-that-you-can-play-forever/\">A Tiny Game That You Can Play Forever</a>:</p>\n<blockquote>\n<p>Asher Vollmer, creator of Threes and Close Castles, joins us to talk puzzle games. We discuss the process of designing developing Threes, and what goes in to making a great little puzzle game. We then talk about the whimsical minimalism that works so well in indie games, and how to bring them to life. We talk about difficulty, complexity, and how to tune games. We touch on the effects of free to play on puzzle games, and how it makes us feel weird.</p>\n</blockquote>\n<p>There're some great points in there. It focuses a lot on Threes' gameplay and evolutions and dives a bit more into minimalism in games.</p>","frontmatter":{"date":"2014-12-19","readableDate":"19 December 2014","title":"A Tiny Game That You Can Play Forever","showPageTitle":null},"fields":{"path":"/2014/12/a-tiny-game-that-you-can-play-forever/"}}},"pathContext":{"id":"/Users/ashen/Code/solarsailer.net/content/posts/2014-12-19-a-tiny-game-that-you-can-play-forever.md absPath of file >>> MarkdownRemark"}}

